.slider {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.slider__item {
  position: relative;
  @media screen and (min-width: 1200px) {
    margin-bottom: 32px;
    margin-left: 20px;
    margin-right: 20px;
  }
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    margin-bottom: 32px;
  }
  @media screen and (min-width: 320px) and (max-width: 767px) {
    margin-bottom: 16px;
  }
}
.slider-text--container {
  background: $color-secondary;
  display: flex;

  @media screen and (min-width: 1200px) {
    margin-left: 116px;
    margin-right: 116px;
    margin-bottom: 16px;
  }
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    width: 620px;
    height: 164px;
    margin-bottom: 16px;
    margin-left: 63px;
  }

  @media screen and (min-width: 320px) and (max-width: 767px) {
    margin-bottom: 16px;
    padding-left: 50px;
    padding-right: 50px;
    margin-left: 20px;
    margin-right: 20px;
  }
}
.slider--text-box {
  display: flex;
  align-items: center;
  justify-content: center;

  @media screen and (min-width: 1200px) {
    padding: 47px 109px 39px 109px;
  }
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    padding: 47px;
    padding-top: 37px;
    padding-right: 55px;
    padding-bottom: 44px;
    padding-left: 51px;
  }
  @media screen and (min-width: 320px) and (max-width: 767px) {
    padding-top: 43px;
  //  padding-left: 50px;
  //    padding-right: 50px;
    // padding-bottom: 17px;
  }
}
.slider-point::before {
  content: '';
  position: absolute;
  left: 50%;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background: $color-quinary;
  box-shadow: -10px 0 0 $color-quinary, 10px 0 0 $color-quinary;
}

.slider-point-home::before {
  content: '';
  left: 49.7%;
  margin-bottom: 32px;
  position: absolute;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: $color-septenary;
  box-shadow: -18px 0 0 $color-septenary, 18px 0 0 $color-septenary;
}
.slider-home{
  display: flex;
  justify-content: center;
  text-align: center;
}

.slider-home__item:not(:last-child) {
  margin-right: 18px;
}

.slider--text {
  text-align: center;
  letter-spacing: 0.04em;
  background: repeating-linear-gradient(rgba(0, 0, 0, 0) 0, rgba(0, 0, 0, 0) 29px, #E1E1E1 29px, rgba(0, 0, 0, 0) 30px);



  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: 1.88;
    z-index: 2;

  }
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    font-size: 14px;
    line-height: 2.14;
    z-index: 2;
  }

  @media screen and (min-width: 320px) and (max-width: 767px) {
    font-size: 12px;
    line-height: 2.58;
    z-index: 2;
    // padding-left: 58px;

    // padding-top: 43px;
    // padding-left: 65px;
    // padding-right: 50px;
    // padding-bottom: 17px;
  }
}

.slider--text::before {
  content: '';
  position: absolute;
  z-index: -1;
  width: 54px;
  height: 40px;
  background-image: url(../images/reviews/quotes/quotes-lg.png);
  @media screen and (min-width: 1200px) {
    margin-left: -4%;
    top: 35%;
    left: 21%;
  }
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    margin-left: -5%;
    top: 33%;
    left: 15%;
  }
  @media screen and (min-width: 320px) and (max-width: 767px) {
    margin-left: -18%;
    top: 27%;
  }
}

.slider-image {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 85px;
  height: 85px;
  border-radius: 44px;
  @media screen and (min-width: 1200px) {
    margin-bottom: 16px;
  }
  @media screen and (min-width: 320px) and (max-width: 767px) {
    margin-bottom: 20px;
  }
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    margin-bottom: 32px;
  }
}

.slider--client--text {
  position: relative;
  text-align: center;

  @media screen and (min-width: 1200px) {
    margin-top: 36px;
    margin-bottom: 32px;
    font-size: 20px;
    line-height: 1.85;
  }

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    margin-top: 60px;
    margin-bottom: 32px;
    font-size: 16px;
    line-height: 1.88%;
  }

  @media screen and (min-width: 320px) and (max-width: 767px) {
    margin-top: 36px;
    margin-bottom: 16px;
    font-size: 14px;
    line-height: 1.86;
  }
}
//   
.swiper {
  @media screen and (min-width: 1200px) {
    height: auto;
  }

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    height: auto;
  }
  @media screen and (min-width: 320px) and (max-width: 767px) {
    height: auto;
  }
}
.swiper-pagination-bullet-active {
  width: 12px;
  height: 12px;
  background-image: url(../images/reviews/home/Vector.png);
  background-color: transparent;
  border-radius: 0%;
}
.swiper-pagination-bullet {
  width: 12px;
  height: 12px;
}

